
.screen {
    position: relative;
    width: 320px;
    height: 561px;
    margin-left: auto;
    margin-right: auto;
    @media screen and (min-width: 768px) {
        width: 768px;
        height: 1125px;
        }
    @media screen and (min-width: 1280px) {
        width: 1280px;  
        height: 700px;           
        }
}

.title {
    font-weight: 700;
    font-size: 32px;
    line-height: 1.4;
    width: 280px;

    padding-top: 60px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0;
    margin-top: 0;
    @media screen and (min-width: 768px) {
        font-size: 68px;
        line-height: 1.5;
        width: 588px;
        padding-top: 80px;
        margin-left: 32px;
        }
    @media screen and (min-width: 1280px) {
        line-height: 1.3;
        width: 500px;
        padding-top: 188px;
        margin-left: 16px;
        }
}
.bg {
    position: absolute;
    top: 48;
    width: 100%;
    @media screen and (min-width: 768px) {
        top: 68px;
    }
}

.pets {
    position: absolute;
    width: 550px;
    top: 168px;
    left: -95px;
        @media screen and (min-width: 768px) {
            width: 900px;
            top: 307px;
        }
        @media screen and (min-width: 1280px) {
            top: 30px;
            left: 363px;
        }
}